.title p {
	border-right: 0.15em solid '#ffb503';
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
	font-size: calc(1vw + 9px);
	white-space: nowrap;
	overflow: hidden;
	line-height: 2;
}
.title p:nth-child(1) {
	width: 14em;
	-webkit-animation: type 3s steps(40, end);
	animation: type 3s steps(40, end);
	-webkit-animation-fill-mode: forwards;
	animation-fill-mode: forwards;
}

.title p:nth-child(2) {
	width: 12em;
	opacity: 0;
	-webkit-animation: type2 3s steps(40, end);
	animation: type2 3s steps(40, end);
	-webkit-animation-delay: 4s;
	animation-delay: 4s;
	-webkit-animation-fill-mode: forwards;
	animation-fill-mode: forwards;
}

.title p:nth-child(3) {
	width: 29em;
	opacity: 0;
	-webkit-animation: type3 5s steps(40, end);
	animation: type3 5s steps(40, end);
	-webkit-animation-delay: 7.5s;
	animation-delay: 7.5s;
	-webkit-animation-fill-mode: forwards;
	animation-fill-mode: forwards;
	font-size: calc(0.5vw + 10px);
}

@keyframes type {
	0% {
		width: 0;
	}
	99.9% {
		border-right: 0.15em solid '#ffb503';
	}
	100% {
		border: none;
	}
}

@-webkit-keyframes type {
	0% {
		width: 0;
	}
	99.9% {
		border-right: 0.15em solid '#ffb503';
	}
	100% {
		border: none;
	}
}

@keyframes type2 {
	0% {
		width: 0;
	}
	1% {
		opacity: 1;
	}
	99.9% {
		border-right: 0.15em solid '#ffb503';
	}
	100% {
		opacity: 1;
		border: none;
	}
}

@-webkit-keyframes type2 {
	0% {
		width: 0;
	}
	1% {
		opacity: 1;
	}
	99.9% {
		border-right: 0.15em solid '#ffb503';
	}
	100% {
		opacity: 1;
		border: none;
	}
}

@keyframes type3 {
	0% {
		width: 0;
	}
	1% {
		opacity: 1;
	}
	99.9% {
		border-right: 0.15em solid '#ffb503';
	}
	100% {
		opacity: 1;
		border: none;
	}
}
